import "./popup.css";

const cheatMenuUrl = {
	get: async () =>
		(await chrome.storage.sync.get("cheat-menu-url"))["cheat-menu-url"],
	set: (url) =>
		chrome.storage.sync.set({
			"cheat-menu-url": url,
		}),
};

document.addEventListener("DOMContentLoaded", async () => {
	/**
	 * @type {HTMLInputElement}
	 */
	const cheatMenuInput = document.querySelector("#cheat-menu-url");
	/**
	 * @type {HTMLButtonElement}
	 */
	const button = document.querySelector("#save-button");

	let cheatMenuURL = await cheatMenuUrl.get();

	if (!cheatMenuURL) {
		await cheatMenuUrl.set(
			"https://raw.githubusercontent.com/X-Loader/XLoaderHack/master/dist/extension-bundle.js"
		);
		cheatMenuURL = await cheatMenuUrl.get();
	}

	cheatMenuInput.value = cheatMenuURL;

	button.addEventListener("click", async () => {
		const validUrl = cheatMenuInput.validity.valid;
		const cheatMenuURL = cheatMenuInput.value;
		validUrl && (await cheatMenuUrl.set(cheatMenuURL));
		const oldText = button.innerText;
		button.innerText = validUrl ? "Saved!" : "Invalid URL";
		setTimeout(() => {
			button.innerText = oldText;
		}, 1000);
	});
});
